import React, { Component } from 'react'
import './css.css';
import utils from '../../library'
import MetaLink from '../MetaLink/MetaLink'

class Cards extends Component {
    MetaList = () => {
        let pioneers=[
        "0x04E3E8818e4E934468b01500c0185C0343918787",
       
]
        let listItems = pioneers.map((addr) =>
          // Correct! Key should be specified inside the array.
          <MetaLink address={addr} key={Math.random()}/>
      
        );
        return (
          <>
            {listItems}
          </>
        );
      }

    render(){
        utils.setHomeColors()
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="cards_flyer"><div id="cards_mainlabel">Cypher's Cards Collections</div>
            <img id="cards_imageMain"  alt="NFT"  src="https://cdn.discordapp.com/attachments/751252665085984882/825465842678235176/cybs_back.png"  onClick={() => this.props.update("Features")}></img>
            <br></br> 
            <div id="cards_sublabel"></div>
        </div>
        <div className="Pioneers_container">
        {this.MetaList()}
</div>
        </div>
        );
    }


}




export default Cards